<template>

    <v-content>
            
      <v-card width="300" class="mx-auto mt-6">
        <v-card-title>
          <h1 class="display-1">Login</h1>
        </v-card-title>
        <v-card-text>
          <v-form>
            <v-text-field 
            label="Username"
            prepend-icon="mdi-account-circle" 
            />
            <v-text-field 
            :type="showPassword ? 'text' : 'password'"
            label="Password"
            prepend-icon="mdi-lock"
            :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
            @click:append="showPassword = !showPassword" />
          </v-form>
        </v-card-text>
        <v-card-actions>
          <v-btn color="success">Register</v-btn>
          <v-spacer></v-spacer>
          <v-btn color="info">Login</v-btn>
        </v-card-actions>
      </v-card>
      <div>
        This is some basic html stuff
      </div>

    </v-content>

</template>

<script>

export default {
  name: 'App',
  data: () => ({
    showPassword: false
  }),
};
</script>